<template>
  <button class="framePoint" :style="{right:`${x}px`,top:`${y}px`, color: `${color}`}" @mouseover="toPointer" @mouseout="toCursor" @click="open" v-popover="{ name: `${id}` }">
    <div class="point">
      <content-bubble :id="id"></content-bubble>
    </div>
  </button>
</template>

<script>
import ContentBubble from './ContentBubble';

export default {
  components: {
    ContentBubble,
  }, 
  name: 'FolioNode',
  props: {
    x: Number,
    y: Number,
    color: String,
    id:String
  },
  methods: {
    open(){
      console.log("open")
      console.log(this.$children[0])
    },
    toPointer(){
      document.body.style.cursor = "pointer"; 
    },
    toCursor(){
      document.body.style.cursor = "default"; 
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.point{
  position: relative;
}
.point:before{
  content: ' \25CF';
  font-size: 20px;
}
.framePoint {
  position: absolute;
  width: 50px;
  height: 50px;
  transform: translateX(25px);
  z-index: 10;
  border: none; 
  background-color: transparent;
}
</style>
